import { Component, OnInit } from '@angular/core';
import {Article} from '../shared/interfaces';
import {switchMap} from '../../../node_modules/rxjs/internal/operators';
import {ActivatedRoute, Params} from '@angular/router';
import {of} from 'rxjs';
import {ArticleService} from '../shared/services/article.service';
import {MaterializeService} from '../shared/classes/materialize.service';

@Component({
  selector: 'app-en-about-article-page',
  templateUrl: './en-about-article-page.component.html',
  styleUrls: ['./en-about-article-page.component.css']
})
export class EnAboutArticlePageComponent implements OnInit {

  article: Article;
  docName;
  visibility = false;

  constructor(private router: ActivatedRoute,
              private articleService: ArticleService) { }

  ngOnInit() {
    this.router.params
      .pipe(
        switchMap((params: Params) => {
          if (params['id']) {
            return this.articleService.getById(params['id']);
          }

          return of(null);
        })
      )
      .subscribe(article => {
        if (article) {
          this.article = article;
          this.docName = article.document.substr(11);
        }
      }, error => {
        MaterializeService.toast(error.error.message);
      });
  }

  changeVisibly(){
    this.visibility = !this.visibility
  }

}
