import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {Article} from '../shared/interfaces';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {ArticleService} from '../shared/services/article.service';
import {switchMap} from '../../../node_modules/rxjs/internal/operators';
import {of} from 'rxjs';
import {MaterializeService} from '../shared/classes/materialize.service';

@Component({
  selector: 'app-article-page',
  templateUrl: './article-page.component.html',
  styleUrls: ['./article-page.component.css']
})
export class ArticlePageComponent implements OnInit {

  @ViewChild('textArea') textAreaRef: ElementRef;
  @ViewChild('textAreaEn') textAreaEnRef: ElementRef;
  @ViewChild('textAreaRef') textAreaRefRef: ElementRef;
  @ViewChild('textAreaRefEn') textAreaRefRefEn: ElementRef;

  form: FormGroup;
  isNew = true;
  article: Article;
  tom = null;

  public optionsRef: Object = {
    placeholderText: 'Список літератури',
    toolbarButtons: ['bold', 'italic', 'underline', 'html', 'inlineStyle', 'lineHeight', '|', 'undo', 'redo'],
  }

  public optionsRefEn: Object = {
    placeholderText: 'References',
    toolbarButtons: ['bold', 'italic', 'underline', 'html', 'inlineStyle', 'lineHeight', '|', 'undo', 'redo'],
  }

  constructor(private router: ActivatedRoute,
              private articleService: ArticleService,
              private route: Router) { }

  ngOnInit() {
    this.form = new FormGroup({
      number: new FormControl(null, Validators.required),
      author: new FormControl(null),
      author_en: new FormControl(null),
      title: new FormControl(null, Validators.required),
      title_en: new FormControl(null, Validators.required),
      pages: new FormControl(null),
      key_words: new FormControl(null),
      key_words_en: new FormControl(null),
      summary: new FormControl(null),
      summary_en: new FormControl(null),
      doi: new FormControl(null),
      email: new FormControl(null),
      biography: new FormControl(null),
      biography_en: new FormControl(null),
      references: new FormControl(null),
      references_en: new FormControl(null),
      document: new FormControl(null)
    });

    this.form.disable();
    this.router.params
      .pipe(
        switchMap((params: Params) => {
          if (params['id']) {
            this.isNew = false;

            return this.articleService.getById(params['id']);
          }

          if (params['tom']) {
            this.tom = params['tom'];
          }

          return of(null);
        })
      )
      .subscribe(article => {
        if (article) {
          this.article = article;
          this.form.patchValue({
            number: article.number,
            author: article.author,
            author_en: article.author_en,
            title: article.title,
            title_en: article.title_en,
            pages: article.pages,
            key_words: article.key_words,
            key_words_en: article.key_words_en,
            summary: article.summary,
            summary_en: article.summary_en,
            doi: article.doi,
            email: article.email,
            biography: article.biography,
            biography_en: article.biography_en,
            references: article.references,
            references_en: article.references_en,
            document: article.document
          });

          MaterializeService.updateTextInputs();
          MaterializeService.textareaAuto(this.textAreaRef);
          MaterializeService.textareaAuto(this.textAreaEnRef);
          MaterializeService.textareaAuto(this.textAreaRefRef);
          MaterializeService.textareaAuto(this.textAreaRefRefEn);
        }

        this.form.enable();
      }, error => {
        MaterializeService.toast(error.error.message);
      });
  }

  onSubmit() {
    let obs$;

    this.form.disable();

    if (this.isNew) {
      obs$ = this.articleService.create(this.tom, this.form.value.author, this.form.value.author_en, this.form.value.number,
        this.form.value.title,  this.form.value.title_en, this.form.value.pages, this.form.value.key_words, this.form.value.key_words_en,
        this.form.value.summary, this.form.value.summary_en, this.form.value.doi, this.form.value.email, this.form.value.biography,
        this.form.value.biography_en, this.form.value.references, this.form.value.references_en, this.form.value.document);
    } else {
      obs$ = this.articleService.update(this.article.id, this.form.value.author, this.form.value.author_en, this.form.value.number,
        this.form.value.title,  this.form.value.title_en, this.form.value.pages, this.form.value.key_words, this.form.value.key_words_en,
        this.form.value.summary, this.form.value.summary_en, this.form.value.doi, this.form.value.email, this.form.value.biography,
        this.form.value.biography_en, this.form.value.references, this.form.value.references_en, this.form.value.document);
    }

    obs$.subscribe(
      article => {
        this.article = article;
        MaterializeService.toast('Зміни збережені.');
        this.form.enable();
      }, error => {
        MaterializeService.toast(error.error.message);
        this.form.enable();
      }
    );
  }

  deleteArticle() {
    const desicion = window.confirm('Ви впевнені що хочите видалити статтю ?');

    if (desicion) {
      this.articleService.delete(this.article.id)
        .subscribe(
          () => MaterializeService.toast('Статтю успішно видалено!'),
          err => MaterializeService.toast(err.error.message),
          () => this.route.navigate([`/admin/tom/${ this.tom }`])
        );
    }
  }
}
