import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Message, Article } from '../interfaces';
import {Observable} from 'rxjs';

@Injectable ({
    providedIn: 'root'
})

export class ArticleService {
  constructor(private http: HttpClient) {}

  getAll(): Observable<Article[]> {
    return this.http.get<Article[]>('/api/articles');
  }

  getById(id: string): Observable<Article> {
    return this.http.get<Article>(`/api/articles/${ id }`);
  }

  // @ts-ignore
  create(tom: string, author?: string, author_en?: string, number: string, title: string, title_en: string,
         pages?: string, key_words?: string, key_words_en?: string, summary?: string,
         summary_en?: string, doi?: string, email?: string, biography?: string, biography_en?: string, references?: string,
         references_en?: string, document?: string): Observable<Article> {
    const fd = new FormData();

    if (author) {
      fd.append('author', author);
    }

    if (author_en) {
      fd.append('author_en', author_en);
    }

    if (pages) {
      fd.append('pages', pages);
    }

    if (key_words) {
      fd.append('key_words', key_words);
    }

    if (key_words_en) {
      fd.append('key_words_en', key_words_en);
    }

    if (summary) {
      fd.append('summary', summary);
    }

    if (summary_en) {
      fd.append('summary_en', summary_en);
    }

    if (doi) {
      fd.append('doi', doi);
    }

    if (email) {
      fd.append('email', email);
    }

    if (biography) {
      fd.append('biography', biography);
    }

    if (biography_en) {
      fd.append('biography_en', biography_en);
    }

    if (references) {
      fd.append('references', references);
    }

    if (references_en) {
      fd.append('references_en', references_en);
    }

    if (document) {
      fd.append('document', document);
    }

    fd.append('tom', tom);
    fd.append('number', number);
    fd.append('title', title);
    fd.append('title_en', title_en);

    return this.http.post<Article>('api/articles', fd);
  }

  // @ts-ignore
  update(id: string, author?: string, author_en?: string, number: string, title: string, title_en: string,
         pages?: string, key_words?: string, key_words_en?: string, summary?: string,
         summary_en?: string, doi?: string, email?: string, biography?: string, biography_en?: string, references?: string,
         references_en?: string, document?: string): Observable<Article> {
    const fd = new FormData();

    if (author) {
      fd.append('author', author);
    }

    if (author_en) {
      fd.append('author_en', author_en);
    }

    if (pages) {
      fd.append('pages', pages);
    }

    if (key_words) {
      fd.append('key_words', key_words);
    }

    if (key_words_en) {
      fd.append('key_words_en', key_words_en);
    }

    if (summary) {
      fd.append('summary', summary);
    }

    if (summary_en) {
      fd.append('summary_en', summary_en);
    }

    if (doi) {
      fd.append('doi', doi);
    }

    if (email) {
      fd.append('email', email);
    }

    if (biography) {
      fd.append('biography', biography);
    }

    if (biography_en) {
      fd.append('biography_en', biography_en);
    }

    if (references) {
      fd.append('references', references);
    }

    if (references_en) {
      fd.append('references_en', references_en);
    }

    if (document) {
      fd.append('document', document);
    }

    fd.append('number', number);
    fd.append('title', title);
    fd.append('title_en', title_en);

    return this.http.put<Article>(`api/articles/${id}`, fd);
  }

  delete(id: string): Observable<Message> {
    return this.http.delete<Message>(`/api/articles/${id}`);
  }
}
