import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {Tom} from '../../shared/interfaces';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {TomsService} from '../../shared/services/toms.service';
import {switchMap} from 'rxjs/internal/operators';
import {of} from 'rxjs';
import {MaterializeService} from '../../shared/classes/materialize.service';

@Component({
  selector: 'app-form-tom-page',
  templateUrl: './form-tom-page.component.html',
  styleUrls: ['./form-tom-page.component.css']
})
export class FormTomPageComponent implements OnInit {

    @ViewChild('textArea') textAreaRef: ElementRef;
    @ViewChild('textAreaEn') textAreaEnRef: ElementRef;

    form: FormGroup;
    isNew = true;
    tom: Tom;

    constructor(private router: ActivatedRoute,
                private tomsService: TomsService,
                private route: Router) { }

    ngOnInit() {
        this.form = new FormGroup({
            name: new FormControl(null, Validators.required),
            name_en: new FormControl(null, Validators.required),
            number: new FormControl(null, Validators.required),
            year: new FormControl(null, Validators.required),
            description: new FormControl(null, Validators.required),
            description_en: new FormControl(null, Validators.required),
            document: new FormControl(null)
        });

        this.form.disable();

        this.router.params
            .pipe(
                switchMap((params: Params) => {
                    if (params['id']) {
                        this.isNew = false;
                        return this.tomsService.getById(params['id']);
                    }

                    return of(null);
                })
            )
            .subscribe(tom => {
                if (tom) {
                    this.tom = tom;
                    this.form.patchValue({
                        name: tom.name,
                        name_en: tom.name_en,
                        number: tom.number,
                        description: tom.description,
                        description_en: tom.description_en,
                        year: tom.year,
                        document: tom.document
                    });

                    MaterializeService.updateTextInputs();
                    MaterializeService.textareaAuto(this.textAreaRef);
                    MaterializeService.textareaAuto(this.textAreaEnRef);
                }

                this.form.enable();
            }, error => {
                MaterializeService.toast(error.error.message);
            });
    }

    onSubmit() {
        let obs$;

        this.form.disable();

        if (this.isNew) {
            obs$ = this.tomsService.create(this.form.value.name, this.form.value.name_en, this.form.value.number,
              this.form.value.year, this.form.value.description, this.form.value.description_en, this.form.value.document);
        } else {
            obs$ = this.tomsService.update(this.tom.id, this.form.value.name, this.form.value.name_en, this.form.value.number,
              this.form.value.year, this.form.value.description, this.form.value.description_en, this.form.value.document);
        }

        obs$.subscribe(
            tom => {
                this.tom = tom;
                MaterializeService.toast('Зміни збережені.');
                this.form.enable();
            }, error => {
                MaterializeService.toast(error.error.message);
                this.form.enable();
            }
        );
    }

    deleteTom() {
        const desicion = window.confirm('Ви впевнені що хочите видалити ' + this.tom.name + ' ' + this.tom.number);

        if (desicion) {
            this.tomsService.delete(this.tom.id)
                .subscribe(
                    res => MaterializeService.toast(res.message),
                    err => MaterializeService.toast(err.error.message),
                    () => this.route.navigate(['/admin'])
                );
        }
    }

}
