import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Message, Tom} from '../interfaces';
import {Observable} from 'rxjs';

@Injectable ({
    providedIn: 'root'
})

export class TomsService {
    constructor(private http: HttpClient) {}

    fetch(): Observable<Tom[]> {
       return this.http.get<Tom[]>('/api/toms');
    }

    getAll(): Observable<Tom[]> {
      return this.http.get<Tom[]>('/api/toms/all');
    }

    getById(id: string): Observable<Tom> {
        return this.http.get<Tom>(`/api/toms/${ id }`);
    }

    create(name: string, name_en: string, number: string, year: string,
           description: string, description_en: string, document?: string): Observable<Tom> {
        const fd = new FormData();

        if (document) {
          fd.append('document', document );
        }

        fd.append('name', name);
        fd.append('name_en', name_en);
        fd.append('number', number);
        fd.append('year', year);
        fd.append('description', description);
        fd.append('description_en', description_en);

        return this.http.post<Tom>('api/toms', fd);
    }

    update(id: string, name: string, name_en: string, number: string, year: string,
           description: string, description_en: string, document?: string): Observable<Tom> {
        const fd = new FormData();

        if (document) {
          fd.append('document', document );
        }

        fd.append('name', name);
        fd.append('name_en', name_en);
        fd.append('number', number);
        fd.append('year', year);
        fd.append('description', description);
        fd.append('description_en', description_en);

        return this.http.put<Tom>(`api/toms/${id}`, fd);
    }

    delete(id: string): Observable<Message> {
        return this.http.delete<Message>(`/api/toms/${id}`);
    }
}
